@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300&family=Lato&family=Montserrat:wght@300&display=swap');

:root {
  --primary-blue: #00d8ff;
  --primary-purple: #6a00ff;
}

@layer base {
  * {
    @apply font-[Kanit]
  }
  h1 {
    @apply text-4xl font-bold
  }

  li {
    @apply p-4
  }
  button {
    @apply bg-gradient-to-r from-[var(--primary-purple)] to-[var(--primary-blue)] py-3 px-7 rounded-3xl
  }
}